import React from "react"
import { Link } from "react-router-dom"
import user from "../assets/icons/user.png"
import AccessDenied from "../components/access-denied"
import Header from "../components/header"
import { getUserRole } from "../utilities/utilities"

const Profile = () => {
    const userData = JSON.parse(localStorage.getItem("userData")).user
    return (
        <div className="home-wrapper">
            <Header backLink={"/home"} homeItemId={1} />
            <div>
                <div className="col-10 mx-auto p-0 pt-4 pb-5">
                    <div className="row m-0">
                        <div className="font-weight-bold text-secondary text-right mb-2">
                            پروفایل
                        </div>
                    </div>
                    <div className="row mt-4">
                        <img
                            className="mx-auto"
                            src={user}
                            style={{ width: "70px", height: "70px" }}
                            alt="Icon"
                        />
                    </div>
                    <div>
                        <div className="row m-0 mt-5">
                            <div className="col-6 pb-2 col-sm-3 font-weight-bold">نام</div>
                            <div className="col-6 pb-2 col-sm-3 text-left text-secondary">
                                {userData.firstName}
                            </div>
                            <div className="col-6 pb-2 col-sm-3 font-weight-bold">نام خانوادگی</div>
                            <div className="col-6 pb-2 col-sm-3 text-left text-secondary">
                                {userData.lastName}
                            </div>
                            <div className="col-6 pb-2 col-sm-3 font-weight-bold">کد پرسنلی</div>
                            <div className="col-6 pb-2 col-sm-3 text-left text-secondary">
                                {userData.personalCode}
                            </div>
                            <div className="col-6 pb-2 col-sm-3 font-weight-bold">شماره موبایل</div>
                            <div className="col-6 pb-2 col-sm-3 text-left text-secondary">
                                {userData.phoneNumber}
                            </div>
                            <div className="col-6 pb-2 col-sm-3 font-weight-bold">شرکت</div>
                            <div className="col-6 pb-2 col-sm-3 text-left text-secondary">
                                {userData.company.name}
                            </div>
                            <div className="col-6 pb-2 col-sm-3 font-weight-bold">سمت</div>
                            <div className="col-6 pb-2 col-sm-3 text-left text-secondary">
                                {userData.role.name}
                            </div>
                        </div>
                    </div>
                    <div className="mt-4">
                        <Link
                            to={{
                                pathname: "/edit-user",
                                formData: {
                                    username: userData.username,
                                    password: userData.password,
                                    firstname: userData.firstName,
                                    lastname: userData.lastName,
                                    personnelCode: userData.personalCode,
                                    phoneNumber: userData.phoneNumber,
                                    role: userData.role.name,
                                    company: userData.company.name,
                                },
                                backPath: "/profile",
                            }}
                            className="mx-auto"
                        >
                            <button type="button" className="btn btn-primary col-12 mb-3">
                                ویرایش پروفایل
                            </button>
                        </Link>
                        <div className="">
                            <Link to="/users" className="mx-auto">
                                <button type="button" className="btn btn-primary col-12 mb-3">
                                    لیست کاربران
                                </button>
                            </Link>
                        </div>
                        <div className="">
                            {getUserRole() === 2 || getUserRole() === 4 || getUserRole() === 5 ? (
                                <AccessDenied>
                                    <button type="button" className="btn btn-secondary col-12 mb-3">
                                        افزدون کاربر
                                    </button>
                                </AccessDenied>
                            ) : (
                                <Link to="/add-user" className="mx-auto">
                                    <button type="button" className="btn btn-primary col-12 mb-3">
                                        افزدون کاربر
                                    </button>
                                </Link>
                            )}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Profile
