import React, { useState } from "react"
import user from "../assets/icons/user.png"
import Header from "../components/header"
import { createUserAsync, getUserCompanyName, getUserRole } from "../utilities/utilities"
import ConfirmationModal from "../components/confirmation-modal"
import ResultModal from "../components/result-modal"
import { companyList } from "../utilities/data"
import { roleList } from "../utilities/data"

const AddUser = () => {
    const [username, setUsername] = useState("")
    const [password, setPassword] = useState("")
    const [firstname, setFirstname] = useState("")
    const [lastname, setLastname] = useState("")
    const [personnelCode, setPersonnelCode] = useState("")
    const [phoneNumber, setPhoneNumber] = useState("")
    const [role, setRole] = useState(getUserRole() === 3 ? roleList[2].id : roleList[0].id)
    const [company, setCompany] = useState(
        getUserRole() === 3 ? getUserCompanyName() : companyList[0].name
    )
    const [pending, setPending] = useState(false)
    const [error, setError] = useState(false)
    const [success, setSuccess] = useState(false)
    const [showConfirmationModal, setShowConfirmationModal] = useState(false)
    const [showResultModal, setShowResultModal] = useState(false)

    const roleOptions = roleList.map((r) => {
        return (
            <option
                disabled={getUserRole() === 3 && (r.id === 1 || r.id === 2)}
                className={getUserRole() === 3 && (r.id === 1 || r.id === 2) && "d-none"}
                value={r.id}
            >
                {r.name}
            </option>
        )
    })

    const companyOptions = companyList.map((c) => {
        return <option value={c.name}>{c.name}</option>
    })

    const submitHandler = async (event) => {
        event.preventDefault()
        setPending(true)
        try {
            await createUserAsync(
                username,
                password,
                phoneNumber,
                role.toString(),
                company,
                firstname,
                lastname,
                personnelCode
            )
            setSuccess(true)
            setPending(false)
            setShowConfirmationModal(false)
            setShowResultModal(true)
        } catch (error) {
            setError(true)
            console.log(error)
            setPending(false)
        }
    }

    return (
        <div className="home-wrapper">
            <Header backLink={"/profile"} homeItemId={1} />
            <div>
                <div className="col-10 mx-auto p-0 pt-4 pb-5">
                    <div className="row m-0">
                        <div className="font-weight-bold text-secondary text-right mb-2">
                            افزودن کاربر
                        </div>
                    </div>
                    <div className="row mt-4">
                        <img
                            className="mx-auto"
                            src={user}
                            style={{ width: "70px", height: "70px" }}
                            alt="Icon"
                        />
                    </div>
                    <div>
                        <div className="row m-0 mt-4">
                            <div className="col-12 text-center bg-danger text-light">
                                {success ? "ثبت کاربر با موفقیت انجام شد" : ""}
                                {error ? "خطا در ثبت کاربر" : ""}
                            </div>
                        </div>
                        <form>
                            <div className="row m-0 mt-5">
                                <div className="col-6 p-0 mb-4 col-sm-3 font-weight-bold">
                                    نام کاربری
                                </div>
                                <div className="col-6 p-0 mb-4 col-sm-3 text-secondary">
                                    <input
                                        name="username"
                                        type="text"
                                        className="sp-add-user-input"
                                        required
                                        disabled={pending}
                                        onChange={(event) => {
                                            setUsername(event.target.value)
                                        }}
                                        value={username}
                                    />
                                </div>
                                <div className="col-6 p-0 mb-4 col-sm-3 font-weight-bold">
                                    رمز عبور
                                </div>
                                <div className="col-6 p-0 mb-4 col-sm-3 text-secondary">
                                    <input
                                        name="password"
                                        type="password"
                                        className="sp-add-user-input"
                                        required
                                        disabled={pending}
                                        onChange={(event) => {
                                            setPassword(event.target.value)
                                        }}
                                        value={password}
                                    />
                                </div>

                                <div className="col-6 p-0 mb-4 col-sm-3 font-weight-bold">نام</div>
                                <div className="col-6 p-0 mb-4 col-sm-3 text-secondary">
                                    <input
                                        name="firstname"
                                        type="text"
                                        className="sp-add-user-input"
                                        required
                                        disabled={pending}
                                        onChange={(event) => {
                                            setFirstname(event.target.value)
                                        }}
                                        value={firstname}
                                    />
                                </div>

                                <div className="col-6 p-0 mb-4 col-sm-3 font-weight-bold">
                                    نام خانوادگی
                                </div>
                                <div className="col-6 p-0 mb-4 col-sm-3 text-secondary">
                                    <input
                                        name="lastname"
                                        type="text"
                                        className="sp-add-user-input"
                                        required
                                        disabled={pending}
                                        onChange={(event) => {
                                            setLastname(event.target.value)
                                        }}
                                        value={lastname}
                                    />
                                </div>

                                <div className="col-6 p-0 mb-4 col-sm-3 font-weight-bold">
                                    کد پرسنلی
                                </div>
                                <div className="col-6 p-0 mb-4 col-sm-3 text-secondary">
                                    <input
                                        name="personnelCode"
                                        type="text"
                                        className="sp-add-user-input"
                                        required
                                        disabled={pending}
                                        onChange={(event) => {
                                            setPersonnelCode(event.target.value)
                                        }}
                                        value={personnelCode}
                                    />
                                </div>

                                <div className="col-6 p-0 mb-4 col-sm-3 font-weight-bold">
                                    شماره موبایل
                                </div>
                                <div className="col-6 p-0 mb-4 col-sm-3 text-secondary">
                                    <input
                                        name="phoneNumber"
                                        type="text"
                                        className="sp-add-user-input"
                                        required
                                        disabled={pending}
                                        onChange={(event) => {
                                            setPhoneNumber(event.target.value)
                                        }}
                                        value={phoneNumber}
                                    />
                                </div>

                                <div className="col-6 p-0 mb-4 col-sm-3 font-weight-bold">سمت</div>
                                <div className="col-6 p-0 mb-4 col-sm-3 text-secondary">
                                    <select
                                        name="role"
                                        id="role"
                                        required
                                        disabled={pending}
                                        onChange={(event) => {
                                            setRole(event.target.value)
                                            if (event.target.value === roleList[0].id.toString())
                                                setCompany(companyList[0].name)
                                        }}
                                        className="form-control"
                                        value={role}
                                    >
                                        {roleOptions}
                                    </select>
                                </div>

                                <div className="col-6 p-0 mb-4 col-sm-3 font-weight-bold">شرکت</div>
                                <div className="col-6 p-0 mb-4 col-sm-3 text-secondary">
                                    {getUserRole() !== 3 && (
                                        <select
                                            name="company"
                                            id="company"
                                            required
                                            disabled={
                                                pending ||
                                                role.toString() === roleList[0].id.toString()
                                            }
                                            onChange={(event) => {
                                                setCompany(event.target.value)
                                            }}
                                            className="form-control"
                                            value={
                                                role.toString() === roleList[0].id.toString()
                                                    ? companyList[0].name
                                                    : company
                                            }
                                        >
                                            {companyOptions}
                                        </select>
                                    )}

                                    {getUserRole() === 3 && (
                                        <select
                                            name="company"
                                            id="company"
                                            required
                                            disabled
                                            onChange={(event) => setCompany(event.target.value)}
                                            className="form-control"
                                            value={getUserCompanyName()}
                                        >
                                            {companyOptions}
                                        </select>
                                    )}
                                </div>
                            </div>
                            <div className="row m-0 mt-4">
                                <button
                                    type="button"
                                    className="btn btn-primary mt-4 mx-auto col-12"
                                    onClick={() => {
                                        setShowConfirmationModal(true)
                                    }}
                                >
                                    ثبت
                                </button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
            {showConfirmationModal ? (
                <ConfirmationModal
                    handler={submitHandler}
                    show={showConfirmationModal}
                    action={"createUser"}
                    pending={pending}
                    onHide={() => {
                        setShowConfirmationModal(false)
                    }}
                />
            ) : null}
            {showResultModal ? <ResultModal show={showResultModal} action={"createUser"} /> : null}
        </div>
    )
}

export default AddUser
