import React from "react"
import { Link } from "react-router-dom"
import success from "../assets/icons/success.png"
import { Modal } from "react-bootstrap"

const ResultModal = (props) => {
    const action = props.action
    return (
        <Modal
            {...props}
            show={props.show}
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Body>
                <div className="row m-0 pt-3">
                    <div className="col-12 text-center">
                        <img
                            src={success}
                            alt="warning"
                            className="warning-icon mb-3"
                        />
                        {action === "sell" ? (
                            <div>
                                <h4 className="text-center mb-3">فروش ملک</h4>
                                <h5 className="text-center mb-1">
                                    ملک جدید با موفقیت فروخته شد
                                </h5>
                            </div>
                        ) : null}
                        {action === "delete" ? (
                            <div>
                                <h4 className="text-center mb-3">حذف ملک</h4>
                                <h5 className="text-center mb-1">
                                    ملک با موفقیت حذف شد
                                </h5>
                            </div>
                        ) : null}
                        {action === "createUser" ? (
                            <div>
                                <h4 className="text-center mb-3">
                                    افزودن کاربر
                                </h4>
                                <h5 className="text-center mb-1">
                                    کاربر جدید با موفقیت افزوده شد
                                </h5>
                            </div>
                        ) : null}
                        {action === "editUser" ? (
                            <div>
                                <h4 className="text-center mb-3">
                                    ویرایش کاربر
                                </h4>
                                <h5 className="text-center mb-1">
                                    اطلاعات کاربر با موفقیت ویرایش شد
                                </h5>
                            </div>
                        ) : null}
                        {action === "createEstate" ? (
                            <div>
                                <h4 className="text-center mb-3">افزودن ملک</h4>
                                <h5 className="text-center mb-1">
                                    ملک جدید با موفقیت افزوده شد
                                </h5>
                            </div>
                        ) : null}
                        {action === "editEstate" ? (
                            <div>
                                <h4 className="text-center mb-3">ویرایش ملک</h4>
                                <h5 className="text-center mb-1">
                                    اطلاعات ملک با موفقیت ویرایش شد
                                </h5>
                            </div>
                        ) : null}
                        {action === "accept" ? (
                            <div>
                                <h4 className="text-center mb-3">تأیید ملک</h4>
                                <h5 className="text-center mb-1">
                                    ملک تأیید شد
                                </h5>
                            </div>
                        ) : null}
                        {action === "reject" ? (
                            <div>
                                <h4 className="text-center mb-3">رد ملک</h4>
                                <h5 className="text-center mb-1">ملک رد شد</h5>
                            </div>
                        ) : null}
                    </div>
                </div>
            </Modal.Body>
            <Modal.Footer>
                <Link to="/home">
                    <button
                        type="button"
                        className="btn btn-success btn-lg pr-5 pl-5 mb-3"
                        onClick={props.onHide}
                    >
                        خانه
                    </button>
                </Link>
            </Modal.Footer>
        </Modal>
    )
}

export default ResultModal
