import React from "react"
import { IoIosArrowBack } from "react-icons/io"
import { useParams } from "react-router"
import { Link } from "react-router-dom"
import AccessDenied from "../components/access-denied"
import { HomePageItems } from "../utilities/data"
import { getUserRole } from "../utilities/utilities"
const RealestateCrud = () => {
    const { id } = useParams()
    const companyName = HomePageItems.find((item) => {
        return Number(item.id) === Number(id)
    }).name
    const filteredItems = HomePageItems?.filter((item) => Number(item.id) === Number(id))
    let item = {}
    if (filteredItems.length) item = filteredItems[0]
    const createHomeItem = (id, name, styles) => {
        return (
            <div className="col-sm-4 col-12 p-0">
                <div className={`w-100 estate-item ${styles} mb-3`} key={name}>
                    <br />
                    <div className="text-left">{name}</div>
                </div>
            </div>
        )
    }
    return (
        <div className="home-wrapper">
            <div className="col-12 row m-0 mx-auto pt-2 bg-white position-fixed z-999">
                <div className="col-2"></div>
                <Link
                    to="/home"
                    className={`img-fluid col-8 home-item-${item?.id} realestate-item`}
                ></Link>
                <Link to="/home" className="col-2 my-auto p-0 text-left">
                    <IoIosArrowBack className="text-dark h3" />
                </Link>
            </div>
            <br />
            <div className="pt-5">
                <div className="col-10 mx-auto p-0 pt-4 pb-5">
                    <div className="row m-0">
                        <div className="font-weight-bold text-secondary text-right mb-2">
                            عملیات
                        </div>
                    </div>

                    {getUserRole() === 2 || getUserRole() === 4 ? (
                        <AccessDenied>
                            <div className="col-12 p-0">
                                {createHomeItem(1, "اضافه کردن ملک", "estate-item-1")}
                            </div>
                        </AccessDenied>
                    ) : (
                        <Link
                            to={{
                                pathname: "/realestate-create",
                                companyName: companyName,
                            }}
                            className="col-12 p-0"
                        >
                            {createHomeItem(1, "اضافه کردن ملک", "estate-item-1")}
                        </Link>
                    )}
                    <div className="row m-0">
                        <div className="font-weight-bold text-secondary text-right mb-2">
                            {item?.name}
                        </div>
                        <div className="col-12 p-0">
                            <Link
                                to={{
                                    pathname: "/realestate-list/" + item?.name,
                                    companyId: item?.id,
                                }}
                            >
                                {createHomeItem(2, " لیست ملک", "estate-item-2")}
                            </Link>
                        </div>
                        <div className="col-12 p-0">
                            <Link
                                to={{
                                    pathname: "/realestate-sold/" + item?.name,
                                    companyId: item?.id,
                                }}
                            >
                                {createHomeItem(3, "فروش رفته ها", "estate-item-3")}
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default RealestateCrud
