import React from "react"
import { useHistory } from "react-router-dom"

export default function NotAllowed() {
    const history = useHistory()
    window.innerWidth < 500 && history.push("/")
    return (
        <div
            className="text-light fw-bold h5 d-flex align-items-center justify-content-center"
            style={{
                height: "100vh",
                width: "100wh",
                backgroundColor: "black",
            }}
        >
            لطفا از گوشی موبایل استفاده کنید
        </div>
    )
}
