import Home from "./pages/home"
import Login from "./pages/login"
import { HashRouter } from "react-router-dom"
import { Redirect, Route, Switch } from "react-router"
import RealestateCrud from "./pages/realestate-crud"
import RealestateCreate from "./pages/realestate-create"
import Profile from "./pages/profile"
import AddUser from "./pages/add-user"
import EditUser from "./pages/edit-user"
import Users from "./pages/users"
import UsersDetail from "./pages/users-detail"
import RealestateList from "./pages/realestate-list"
import RealestateSold from "./pages/realestate-sold"
import { ProtectedRoute } from "./components/protected-route"
import RealestateEdit from "./pages/realestate-edit"
import RealestateDetail from "./pages/realestate-detail"
import RealestateUnconfirmed from "./pages/realestate-unconfirmed"
import NotAllowed from "./pages/not-allowed"

const App = () => {
    return (
        <HashRouter basename="tidewater">
            <Switch>
                <Route exact path="/not-allowed" component={NotAllowed} />
                {window.innerWidth > 500 && <Redirect to="/not-allowed" />}
                <Route exact path="/login" component={Login} />
                <ProtectedRoute exact path="/home" component={Home} />
                <ProtectedRoute
                    exact
                    path="/realestate-crud/:id"
                    component={RealestateCrud}
                />
                <ProtectedRoute
                    exact
                    path="/realestate-create"
                    component={RealestateCreate}
                />
                <ProtectedRoute
                    exact
                    path="/realestate-edit"
                    component={RealestateEdit}
                />
                <ProtectedRoute exact path="/profile" component={Profile} />
                <ProtectedRoute exact path="/add-user" component={AddUser} />
                <ProtectedRoute exact path="/edit-user" component={EditUser} />
                <ProtectedRoute
                    exact
                    path="/users/:userId"
                    component={UsersDetail}
                />
                <ProtectedRoute exact path="/users" component={Users} />
                <ProtectedRoute
                    exact
                    path="/realestate-list"
                    component={RealestateList}
                />
                <ProtectedRoute
                    exact
                    path="/realestate-list/:companyName"
                    component={RealestateList}
                />
                <ProtectedRoute
                    exact
                    path="/realestate-sold"
                    component={RealestateSold}
                />
                <ProtectedRoute
                    exact
                    path="/realestate-sold/:companyName"
                    component={RealestateSold}
                />
                <ProtectedRoute
                    exact
                    path="/realestate-detail/:estateId"
                    component={RealestateDetail}
                />
                <ProtectedRoute
                    exact
                    path="/realestate-unconfirmed"
                    component={RealestateUnconfirmed}
                />
                <Redirect to="/login" />
            </Switch>
        </HashRouter>
    )
}

export default App
