import React, { useEffect, useState } from "react"
import { useParams } from "react-router"
import Header from "../components/header"
import ConfirmationModal from "../components/confirmation-modal"
import { estateSoldAsync, getUserRole } from "../utilities/utilities"
import { Carousel, Button } from "react-bootstrap"
import { Link } from "react-router-dom"
import { HomePageItems } from "../utilities/data"
import noImage from "../assets/no-image.png"
import { estateDeleteAsync } from "../utilities/utilities"
import ResultModal from "../components/result-modal"

const RealestateSold = () => {
    const { companyName } = useParams()
    let homeItemId
    if (companyName) {
        homeItemId = HomePageItems.find((item) => {
            return item.name === companyName
        }).id
    }
    const [estates, setEstates] = useState([])
    const [pending, setPending] = useState(false)
    const [loadPending, setLoadPending] = useState(false)
    const [activeEstate, setActiveEstate] = useState({})
    const [showConfirmationModal, setShowConfirmationModal] = useState(false)
    const [showResultModal, setShowResultModal] = useState(false)

    const estateDeleteHandler = async (event) => {
        event.preventDefault()
        setPending(true)
        try {
            await estateDeleteAsync(activeEstate.id)
            setPending(false)
            setShowConfirmationModal(false)
            setShowResultModal(true)
        } catch (error) {
            setPending(false)
            console.log(error)
        }
    }

    useEffect(() => {
        ;(async () => {
            if (companyName) {
                setLoadPending(true)
                const fetchedEstates = await estateSoldAsync(companyName)
                setEstates(fetchedEstates)
                setLoadPending(false)
            } else {
                setLoadPending(true)
                const fetchedEstates = await estateSoldAsync()
                setEstates(fetchedEstates)
                setLoadPending(false)
            }
        })()
    }, [companyName])

    const estatesList = estates.map((e) => {
        let images = []
        if (e.image1) {
            images.push(e.image1)
        }
        if (e.image2) {
            images.push(e.image2)
        }
        if (e.image3) {
            images.push(e.image3)
        }

        let imagesCarouselItems
        let carouselIndicators = true

        if (images.length !== 0) {
            imagesCarouselItems = images.map((i) => {
                return (
                    <Carousel.Item key={i}>
                        <Link
                            to={{
                                pathname: `/realestate-detail/${e.id}`,
                                estateData: e,
                            }}
                        >
                            <div className="estate-list-img-container">
                                <img
                                    src={"https://pwa.tidewater.ir:2026/estate/image/" + i}
                                    alt="realestate"
                                    className="estate-list-img"
                                />
                            </div>
                        </Link>
                    </Carousel.Item>
                )
            })
        } else {
            carouselIndicators = false
            imagesCarouselItems = (
                <Carousel.Item>
                    <img src={noImage} alt="realestate" className="estate-list-img-container" />
                </Carousel.Item>
            )
        }

        return (
            <div className="col-11 col-sm-4 mx-auto">
                <Carousel
                    interval={null}
                    prevLabel=""
                    nextLabel=""
                    nextIcon={null}
                    prevIcon={null}
                    className="mb-0"
                    defaultActiveIndex={0}
                    indicators={carouselIndicators}
                >
                    {imagesCarouselItems}
                </Carousel>
                <div className="row m-0 mt-2">
                    <div className="col-8">
                        <Link
                            to={{
                                pathname: `/realestate-detail/${e.id}`,
                                estateData: e,
                            }}
                        >
                            <h5 className="text-dark">{e.name}</h5>
                        </Link>
                    </div>
                    <div className="col-2">
                        <span className="btn btn-primary fa fa-edit"></span>
                    </div>
                    <div className="col-2">
                        {getUserRole() === 2 || getUserRole() === 4 || getUserRole() === 5 ? (
                            <Button className="btn btn-secondary fa fa-trash-alt"></Button>
                        ) : (
                            <Button
                                variant="danger"
                                className="btn btn-danger fa fa-trash-alt"
                                onClick={() => {
                                    setActiveEstate(e)
                                    setShowConfirmationModal(true)
                                }}
                            ></Button>
                        )}
                    </div>
                </div>
                <div className="row m-0 mt-1">
                    <div className="col-12 mb-4">
                        <p className="text-secondary mb-0">{e.address}</p>
                        <p className="text-secondary mb-0">خریدار: {e.buyer}</p>
                        <p className="text-secondary mb-0">شماره مزایده: -</p>
                        <p className="text-secondary mb-0">تاریخ فروش: {e.soldDate}</p>
                    </div>
                </div>
            </div>
        )
    })

    return (
        <div>
            <Header
                backLink={companyName ? "/realestate-crud/" + homeItemId : "/home"}
                homeItemId={homeItemId || 1}
            />
            <div className="home-wrapper overflow-auto py-4">
                <div className="col-10 mx-auto p-0 pt-4 pb-4">
                    <div className="row m-0 mt-5 text-secondary font-weight-bold justify-content-sm-center">
                        املاک
                    </div>
                </div>
                {loadPending ? (
                    <div className="text-center">
                        <button className="btn btn-primary">
                            <span className="spinner-border"></span>
                        </button>
                    </div>
                ) : null}
                <div className="row m-0">{estatesList}</div>
                {showConfirmationModal ? (
                    <ConfirmationModal
                        handler={estateDeleteHandler}
                        show={showConfirmationModal}
                        action={"delete"}
                        pending={pending}
                        onHide={() => {
                            setShowConfirmationModal(false)
                        }}
                    />
                ) : null}
                {showResultModal ? <ResultModal show={showResultModal} action={"delete"} /> : null}
            </div>
        </div>
    )
}

export default RealestateSold
