import React from "react"
import warning from "../assets/icons/warning.png"
import { Modal } from "react-bootstrap"

function ConfirmationModal(props) {
    return (
        <div>
            <Modal
                {...props}
                show={props.show}
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Modal.Header closeButton></Modal.Header>
                <Modal.Body>
                    <div className="row m-0">
                        {props.action === "delete" ? (
                            <div className="col-12 text-center">
                                <img src={warning} alt="warning" className="warning-icon mb-3" />
                                <h4 className="text-center mb-3">عملیات حذف ملک</h4>
                                <h5 className="text-center mb-3">آیا مطمئن هستید؟</h5>
                            </div>
                        ) : null}
                        {props.action === "createUser" ? (
                            <div className="col-12 text-center">
                                <img src={warning} alt="warning" className="warning-icon mb-3" />
                                <h4 className="text-center mb-3">عملیات افزودن کاربر</h4>
                                <h5 className="text-center mb-3">آیا مطمئن هستید؟</h5>
                            </div>
                        ) : null}
                        {props.action === "editUser" ? (
                            <div className="col-12 text-center">
                                <img src={warning} alt="warning" className="warning-icon mb-3" />
                                <h4 className="text-center mb-3">عملیات ویرایش کاربر</h4>
                                <h5 className="text-center mb-3">آیا مطمئن هستید؟</h5>
                            </div>
                        ) : null}
                        {props.action === "createEstate" ? (
                            <div className="col-12 text-center">
                                <img src={warning} alt="warning" className="warning-icon mb-3" />
                                <h4 className="text-center mb-3">عملیات افزودن ملک</h4>
                                <h5 className="text-center mb-3">آیا مطمئن هستید؟</h5>
                            </div>
                        ) : null}
                        {props.action === "editEstate" ? (
                            <div className="col-12 text-center">
                                <img src={warning} alt="warning" className="warning-icon mb-3" />
                                <h4 className="text-center mb-3">عملیات ویرایش ملک</h4>
                                <h5 className="text-center mb-3">آیا مطمئن هستید؟</h5>
                            </div>
                        ) : null}
                        {props.action === "accept" ? (
                            <div className="col-12 text-center">
                                <img src={warning} alt="warning" className="warning-icon mb-3" />
                                <h4 className="text-center mb-3">تأیید کردن ملک</h4>
                                <h5 className="text-center mb-3">آیا مطمئن هستید؟</h5>
                            </div>
                        ) : null}
                        {props.action === "reject" ? (
                            <div className="col-12 text-center">
                                <img src={warning} alt="warning" className="warning-icon mb-3" />
                                <h4 className="text-center mb-3">رد کردن ملک</h4>
                                <h5 className="text-center mb-3">آیا مطمئن هستید؟</h5>
                            </div>
                        ) : null}
                    </div>
                </Modal.Body>
                <Modal.Footer>
                    <button
                        type="button"
                        className="btn btn-secondary btn-lg"
                        onClick={props.onHide}
                        disabled={props.pending}
                    >
                        بازگشت
                    </button>
                    {props.action === "delete" ? (
                        <button
                            type="button"
                            className="btn btn-danger btn-lg"
                            onClick={props.handler}
                        >
                            {props.pending ? (
                                <span className="spinner-border spinner-border-sm ml-2"></span>
                            ) : null}
                            حذف
                        </button>
                    ) : null}
                    {props.action === "createUser" || props.action ==="createEstate" ? (
                        <button
                            type="button"
                            className="btn btn-success btn-lg"
                            onClick={props.handler}
                        >
                            {props.pending ? (
                                <span className="spinner-border spinner-border-sm ml-2"></span>
                            ) : null}
                            افزودن
                        </button>
                    ) : null}
                    {props.action === "editUser" || props.action ==="editEstate" ? (
                        <button
                            type="button"
                            className="btn btn-success btn-lg"
                            onClick={props.handler}
                        >
                            {props.pending ? (
                                <span className="spinner-border spinner-border-sm ml-2"></span>
                            ) : null}
                            ویرایش
                        </button>
                    ) : null}
                    {props.action === "accept" ? (
                        <button
                            type="button"
                            className="btn btn-success btn-lg"
                            onClick={props.handler}
                        >
                            {props.pending ? (
                                <span className="spinner-border spinner-border-sm ml-2"></span>
                            ) : null}
                            تأیید
                        </button>
                    ) : null}
                    {props.action === "reject" ? (
                        <button
                            type="button"
                            className="btn btn-danger btn-lg"
                            onClick={props.handler}
                        >
                            {props.pending ? (
                                <span className="spinner-border spinner-border-sm ml-2"></span>
                            ) : null}
                            رد
                        </button>
                    ) : null}
                </Modal.Footer>
            </Modal>
        </div>
    )
}

export default ConfirmationModal
