export const imageUpdateAsync = async (estateId, file1, file2, file3) => {
    const body = new FormData()
    body.append("id", estateId)
    if (file1 !== null) {
        body.append("image1", file1)
        body.append("i1Updated", "true")
    } else {
        body.append("i1Updated", "false")
    }
    if (file2 !== null) {
        body.append("image2", file2)
        body.append("i2Updated", "true")
    } else {
        body.append("i2Updated", "false")
    }
    if (file3 !== null) {
        body.append("image3", file3)
        body.append("i3Updated", "true")
    } else {
        body.append("i3Updated", "false")
    }
    const data = await networkRequest("POST", "/estate/image", body)
    return data
}

