export const usersAsync = async () => {
    const data = await networkRequest("GET", "/user", null)
    return data
}

export const createUserAsync = async (
    username,
    password,
    phoneNumber,
    role,
    company,
    firstname,
    lastname,
    personnelCode
) => {
    const data = await networkRequest("POST", "/auth/register", {
        username,
        password,
        phonenumber: phoneNumber,
        role,
        company,
        firstname,
        lastname,
        personalcode: personnelCode,
    })
    return data
}

