import React, { useState } from "react"
import tools from "../assets/icons/tools.png"
import "reactjs-bottom-navigation/dist/index.css"
import jumbo from "../assets/home-jumbotron.jpeg"
import vehicle from "../assets/icons/vehicle.png"
import newUser from "../assets/icons/newUser.png"
import { Link, Redirect } from "react-router-dom"
import insurance from "../assets/icons/insurance.png"
import realestate from "../assets/icons/realestate.png"
import BottomNavigation from "reactjs-bottom-navigation"
import Header from "../components/header"
import { companyList } from "../utilities/data"
import { getUserCompanyName, getUserRole } from "../utilities/utilities"
import AccessDenied from "../components/access-denied"
const Home = () => {
    const [bottomNavigationId, setBottomNavigationId] = useState(1)
    const bottomNavItems = [
        {
            id: 1,
            title: "املاک",
            icon: <img src={realestate} className="bottom-nav-bar-icon" alt="Icon" />,
            activeIcon: <img src={realestate} className="bottom-nav-bar-icon" alt="Icon" />,
        },
        {
            id: 2,
            title: "خودرو",
            icon: <img src={vehicle} className="bottom-nav-bar-icon" alt="Icon" />,
            activeIcon: <img src={vehicle} className="bottom-nav-bar-icon" alt="Icon" />,
        },
        {
            id: 3,
            icon: <img src={newUser} className="profile-bottom-nav-bar-icon" alt="Icon" />,
            activeIcon: <img src={newUser} className="profile-bottom-nav-bar-icon" alt="Icon" />,
        },
        {
            id: 4,
            title: "تجهیزات",
            icon: <img src={tools} className="bottom-nav-bar-icon" alt="Icon" />,
            activeIcon: <img src={tools} className="bottom-nav-bar-icon" alt="Icon" />,
        },
        {
            id: 5,
            title: "بیمه",
            icon: <img src={insurance} className="bottom-nav-bar-icon" alt="Icon" />,
            activeIcon: <img src={insurance} className="bottom-nav-bar-icon" alt="Icon" />,
        },
    ]
    const createHomeItem = (id, name, styles) => {
        return getUserRole() === 3 || getUserRole() === 4 || getUserRole() === 5 ? (
            getUserCompanyName() === name ? (
                <Link to={`realestate-crud/${id}`}>
                    <div className={`w-100 home-item ${styles}`} key={name}>
                        <br />
                        <div className="text-left">{name}</div>
                    </div>
                </Link>
            ) : (
                <AccessDenied>
                    <div
                        className={`w-100 home-item bg-secondary box-shadow-gray ${styles}`}
                        key={name}
                    >
                        <br />
                        <div className="text-left">{name}</div>
                    </div>
                </AccessDenied>
            )
        ) : (
            <Link to={`realestate-crud/${id}`}>
                <div className={`w-100 home-item  ${styles}`} key={name}>
                    <br />
                    <div className="text-left">{name}</div>
                </div>
            </Link>
        )
    }

    if (bottomNavigationId === 1) {
        return (
            <div>
                <Header backLink={"/login"} homeItemId={1} isHomePage />
                <br />
                <div className="col-7 mx-auto font-weight-bold pt-5 text-center">
                    سامانه اطلاعات دارایی ها
                </div>
                <div className="col-12 p-0 text-center">
                    <img src={jumbo} className="img-fluid" alt="Jumbo" />
                </div>
                <div className="home-wrapper">
                    <div className="col-10 mx-auto p-0 pt-4 pb-5">
                        <div className="d-flex justify-content-between align-items-center">
                            <div className="font-weight-bold text-secondary text-center text-sm-right">
                                شرکت ها
                            </div>
                            {getUserRole() === 5 ? (
                                <AccessDenied>
                                    <div className="font-weight-bold text-center fs-14px text-secondary">
                                        املاک تأیید نشده
                                    </div>
                                </AccessDenied>
                            ) : (
                                <div className="font-weight-bold text-center fs-14px">
                                    <Link to="/realestate-unconfirmed">املاک تأیید نشده</Link>
                                </div>
                            )}
                            {getUserRole() === 5 ? (
                                <AccessDenied>
                                    <div className="font-weight-bold text-secondary text-center  fs-14px">
                                        نمایش همه املاک
                                    </div>
                                </AccessDenied>
                            ) : (
                                <div className="font-weight-bold text-primary text-center  fs-14px">
                                    <Link to="/realestate-list">نمایش همه املاک</Link>
                                </div>
                            )}
                        </div>
                        <div className="my-4">
                            <div className="col-12 mb-4">
                                {createHomeItem(1, "تایدواتر خاورمیانه", "home-item-1")}
                            </div>
                            <div className="row m-0">
                                {companyList.map((item, index) =>
                                    index === 0 ? (
                                        <></>
                                    ) : (
                                        <div className="col-6 col-sm-4 mb-3">
                                            {createHomeItem(
                                                index + 1,
                                                item.name,
                                                `home-item-${index + 1}`
                                            )}
                                        </div>
                                    )
                                )}
                            </div>
                        </div>
                    </div>
                </div>
                <div className="d-sm-none d-block">
                    <BottomNavigation
                        items={bottomNavItems}
                        defaultSelected={0}
                        onItemClick={(item) => {
                            setBottomNavigationId(item.id)
                        }}
                    />
                </div>
            </div>
        )
    }

    if (bottomNavigationId === 2) {
        return (
            <div>
                <div className="container">
                    <div className="row m-0">
                        <div className="col-12 mt-3">
                            <h4 className="text-center">تاید واتر</h4>
                        </div>
                    </div>
                    <div className="col-10 mx-auto p-0 pt-4 pb-5">
                        <div className="row m-0">
                            <div className="col-6 p-0 font-weight-bold text-secondary text-right">
                                عملیات
                            </div>
                            <div className="col-6 p-0 font-weight-bold text-primary text-left">
                                <span href="#">بیشتر</span>
                            </div>
                        </div>
                        <div className="my-4">
                            <div className="row m-0 mt-4">
                                <div className="col-6 pr-0 pl-2">
                                    <Link to="/home">
                                        <div className="w-100 action-item action-item-1">
                                            <br />
                                            <div className="text-left">املاک</div>
                                        </div>
                                    </Link>
                                </div>
                                <div className="col-6 pl-0 pr-2">
                                    <Link to="/home">
                                        <div className="w-100 action-item action-item-2">
                                            <br />
                                            <div className="text-left">تجهیزات</div>
                                        </div>
                                    </Link>
                                </div>
                            </div>
                            <div className="row m-0 mt-4">
                                <div className="col-6 pr-0 pl-2">
                                    <Link to="/home">
                                        <div className="w-100 action-item action-item-3">
                                            <br />
                                            <div className="text-left">داشبورد</div>
                                        </div>
                                    </Link>
                                </div>
                                <div className="col-6 pl-0 pr-2">
                                    <Link to="/home">
                                        <div className="w-100 action-item action-item-4">
                                            <br />
                                            <div className="text-left">بیمه</div>
                                        </div>
                                    </Link>
                                </div>
                            </div>
                        </div>
                        <div className="row m-0 mt-5">
                            <div className="col-6 p-0 font-weight-bold text-secondary text-right">
                                پیام
                            </div>
                            <div className="col-6 p-0 font-weight-bold text-primary text-left">
                                <span href="#">بیشتر</span>
                            </div>
                        </div>
                        <div className="row m-0 mt-5">
                            <div className="col-12 mt-5">
                                <p className="text-secondary text-center">پیامی یافت نشد</p>
                            </div>
                        </div>
                    </div>
                </div>
                <BottomNavigation
                    items={bottomNavItems}
                    defaultSelected={1}
                    onItemClick={(item) => {
                        setBottomNavigationId(item.id)
                    }}
                />
            </div>
        )
    }

    if (bottomNavigationId === 3) {
        return <Redirect to="/profile" />
    }

    if (bottomNavigationId === 4) {
        return (
            <div>
                <div>
                    <h2 className="text-center pt-5">تجهیزات</h2>
                </div>

                <BottomNavigation
                    items={bottomNavItems}
                    defaultSelected={3}
                    onItemClick={(item) => {
                        setBottomNavigationId(item.id)
                    }}
                />
            </div>
        )
    }

    if (bottomNavigationId === 5) {
        return (
            <div>
                <div>
                    <h2 className="text-center pt-5">بیمه</h2>
                </div>

                <BottomNavigation
                    items={bottomNavItems}
                    defaultSelected={4}
                    onItemClick={(item) => {
                        setBottomNavigationId(item.id)
                    }}
                />
            </div>
        )
    }
}

export default Home
