import React from "react"
import { Carousel } from "react-bootstrap"
import Header from "../components/header"
import { HomePageItems } from "../utilities/data"
import noImage from "../assets/no-image.png"
import { formsSelectData } from "../utilities/data"

const RealestateDetail = (props) => {
    const estate = props.location.estateData
    const name = estate.name
    const address = estate.address
    const group = estate.group
    const areaAyan = estate.areaAyan
    const yearofConstruction = estate.yearofConstruction
    const identityNumber = estate.identityNumber
    const location = estate.location
    const documentStatus = estate.documentStatus
    const documentType = estate.documentType
    const ownership = estate.ownership
    const beneficiary = estate.beneficiary
    const usage = estate.usage
    const areaArse = estate.areaArse
    const price = estate.price
    const lift = estate.lift ? true : false
    const parking = estate.parking ? true : false
    const storage = estate.storage ? true : false
    const company = estate.company.name

    const homeItemId = HomePageItems.find((item) => {
        return item.name === company
    }).id

    let images = []
    if (estate.image1) {
        images.push(estate.image1)
    }
    if (estate.image2) {
        images.push(estate.image2)
    }
    if (estate.image3) {
        images.push(estate.image3)
    }

    let imagesCarouselItems
    if (images.length !== 0) {
        imagesCarouselItems = images.map((i) => {
            return (
                <Carousel.Item key={i}>
                    <div className="h-70-vw">
                        <img
                            src={"https://pwa.tidewater.ir:2026/estate/image/" + i}
                            alt="realestate"
                            className="estate-list-img"
                        />
                    </div>
                </Carousel.Item>
            )
        })
    } else {
        imagesCarouselItems = (
            <Carousel.Item>
                <img src={noImage} alt="realestate" className="estate-list-img-container" />
            </Carousel.Item>
        )
    }

    return (
        <div>
            <Header backLink={`/realestate-list/${company}`} homeItemId={homeItemId} />
            <div className="home-wrapper overflow-auto pb-4 pt-5">
                <div className="row m-0 mt-5">
                    <div className="col-12">
                        <Carousel
                            interval={null}
                            prevLabel=""
                            nextLabel=""
                            nextIcon={null}
                            prevIcon={null}
                            className="mb-0"
                            defaultActiveIndex={0}
                        >
                            {imagesCarouselItems}
                        </Carousel>
                    </div>
                </div>
                <div className="col-10 mx-auto p-0 pt-2">
                    <div className="row m-0">
                        <h4>{name}</h4>
                    </div>
                    <div className="row m-0">
                        <h5>{address}</h5>
                    </div>

                    <div className="row m-0">
                        <form className="col-12 p-0">
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mb-2"></div>
                            <div className="row flex-nowrap of-scroll sb-hidden text-center">
                                <div className="col-3 col-sm-2 pl-2 pr-2 ml-3 carousel-wrapper">
                                    <h6 className="text-center">گروه</h6>
                                    <h5 className="text-center text-dark">
                                        {formsSelectData.group[group]}
                                    </h5>
                                </div>
                                <div className="col-3 col-sm-2 pl-2 pr-2 ml-3 carousel-wrapper">
                                    <h6 className="text-center">متراژ عیان</h6>
                                    <h5 className="text-center text-dark">{areaAyan}</h5>
                                </div>
                                <div className="col-3 col-sm-2 pl-2 pr-2 ml-3 carousel-wrapper">
                                    <h6 className="text-center">سال ساخت</h6>
                                    <h5 className="text-center text-dark">{yearofConstruction}</h5>
                                </div>
                                <div className="col-3 col-sm-2 pl-2 pr-2 ml-3 carousel-wrapper">
                                    <h6 className="text-center">کد اموال</h6>
                                    <h5 className="text-center text-dark">{identityNumber}</h5>
                                </div>
                                <div className="col-3 col-sm-2 pl-2 pr-2 ml-3 carousel-wrapper">
                                    <h6 className="text-center">محل استقرار</h6>
                                    <h5 className="text-center text-dark">{location}</h5>
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mt-3">
                                <div className="col-4 p-0 font-weight-bold text-dark">مالکیت</div>
                                <div className="col-8 p-0 text-left text-secondary">
                                    {ownership}
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mt-3">
                                <div className="col-4 p-0 font-weight-bold text-dark">
                                    متراژ عرصه
                                </div>
                                <div className="col-8 p-0 text-left text-secondary">{areaArse}</div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mt-3">
                                <div className="col-4 p-0 font-weight-bold text-dark">
                                    وضعیت سند
                                </div>
                                <div className="col-8 p-0 text-left text-secondary">
                                    {formsSelectData.documentStatus[documentStatus]}
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mt-3">
                                <div className="col-4 p-0 font-weight-bold text-dark">کاربری</div>
                                <div className="col-8 p-0 text-left text-secondary">
                                    {formsSelectData.usage[usage]}
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mt-3">
                                <div className="col-4 p-0 font-weight-bold text-dark">نوع سند</div>
                                <div className="col-8 p-0 text-left text-secondary">
                                    {formsSelectData.documentType[documentType]}
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mt-3">
                                <div className="col-4 p-0 font-weight-bold text-dark">آدرس</div>
                                <div className="col-8 p-0 text-left text-secondary">{address}</div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mt-3">
                                <div className="col-4 p-0 font-weight-bold text-dark">
                                    برآورد ریالی
                                </div>
                                <div className="col-8 p-0 text-left text-secondary">
                                    {price} میلیارد ریال
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mt-3">
                                <div className="col-4 p-0 font-weight-bold text-dark">
                                    بهره بردار
                                </div>
                                <div className="col-8 p-0 text-left text-secondary">
                                    {beneficiary}
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mt-3">
                                <div className="col-2 p-0 font-weight-bold text-dark">امکانات</div>
                                <div className="col-10 text-left pl-0">
                                    <label>
                                        <input type="checkbox" checked={lift} />
                                        <span className="text-secondary my-auto mr-2">آسانسور</span>
                                    </label>
                                    <label className="mr-2">
                                        <input type="checkbox" checked={parking} />
                                        <span className="text-secondary my-auto mr-2">پارکینگ</span>
                                    </label>
                                    <label className="mr-2">
                                        <input type="checkbox" checked={storage} />
                                        <span className="text-secondary my-auto mr-2">انباری</span>
                                    </label>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default RealestateDetail
