import React, { useState } from "react"
import { useHistory } from "react-router"
import logo from "../assets/logo.png"
import ErrorModal from "../components/error-modal"
import Spinner from "../components/spinner"
import { loginAsync } from "../utilities/utilities"
const Login = () => {
    const [username, setUsername] = useState("")
    const [password, setPassword] = useState("")
    const [error, setError] = useState("")
    const [showErrorModal, setShowErrorModal] = useState(false)
    const history = useHistory()
    const [pending, setPending] = useState(false)
    const onLoginClick = async (e) => {
        e.preventDefault()
        setPending(true)
        let loginData
        try {
            loginData = await loginAsync(username, password)
            localStorage.setItem("userData", JSON.stringify(loginData))
            setPending(false)
            history.push("/home")
        } catch (error) {
            setError("نام کاربری یا رمز عبور اشتباه است")
            setShowErrorModal(true)
            setPending(false)
        }
    }
    return (
        <div className="login-wrapper">
            <div className="col-7 col-sm-3 mx-auto pt-150 pb-4">
                <img src={logo} className="img-fluid" alt="Logo" />
            </div>
            <div className="row m-0">
                {error && (
                    <ErrorModal
                        showModal={showErrorModal}
                        setShowModal={setShowErrorModal}
                        error={error}
                    />
                )}
            </div>
            <div className="col-11 col-sm-4 mx-auto">
                <form>
                    <input
                        type="text"
                        className="login-input-field form-control"
                        placeholder="نام‌کاربری"
                        disabled={pending}
                        value={username}
                        onChange={(e) => setUsername(e.target.value)}
                    />
                    <br />
                    <input
                        type="password"
                        className="login-input-field form-control"
                        placeholder="کلمه‌عبور"
                        disabled={pending}
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                    />
                    <label className="mt-3">
                        <input
                            type="checkbox"
                            className="my-auto align-middle ml-2"
                        />
                        <span className="text-dark my-auto">
                            مرا به خاطر داشته باش
                        </span>
                    </label>
                    <br />
                    <button
                        onClick={onLoginClick}
                        disabled={pending}
                        className="btn btn-primary pt-0 w-100 br-20 mt-5 login-btn"
                    >
                        {pending && <Spinner />}
                        <span className="mr-3">ورود</span>
                    </button>
                </form>
            </div>
        </div>
    )
}

export default Login
