import React, { useEffect, useState } from "react"
import Header from "../components/header"
import ConfirmationModal from "../components/confirmation-modal"
import { Carousel, Button } from "react-bootstrap"
import { Link } from "react-router-dom"
import noImage from "../assets/no-image.png"
import ResultModal from "../components/result-modal"
import { estateUnconfirmedAsync } from "../utilities/utilities"
import { estateConfirmAsync } from "../utilities/utilities"

const RealestateUnconfirmed = () => {
    const [estates, setEstates] = useState([])
    const [acceptance, setAcceptance] = useState(false)
    const [action, setAction] = useState("")
    const [pending, setPending] = useState(false)
    const [loadPending, setLoadPending] = useState(false)
    const [activeEstate, setActiveEstate] = useState({})
    const [showConfirmationModal, setShowConfirmationModal] = useState(false)
    const [showResultModal, setShowResultModal] = useState(false)

    const estateConfirmHandler = async (event) => {
        event.preventDefault()
        setPending(true)
        try {
            await estateConfirmAsync(activeEstate.id, acceptance)
            setPending(false)
            setShowConfirmationModal(false)
            setShowResultModal(true)
        } catch (error) {
            setPending(false)
            console.log(error)
        }
    }

    useEffect(() => {
        ;(async () => {
            setLoadPending(true)
            const fetchedEstates = await estateUnconfirmedAsync()
            setEstates(fetchedEstates)
            setLoadPending(false)
        })()
    }, [])

    const estatesList = estates.map((e) => {
        let images = []
        if (e.image1) {
            images.push(e.image1)
        }
        if (e.image2) {
            images.push(e.image2)
        }
        if (e.image3) {
            images.push(e.image3)
        }

        let imagesCarouselItems
        let carouselIndicators = true

        if (images.length !== 0) {
            imagesCarouselItems = images.map((i) => {
                return (
                    <Carousel.Item key={i}>
                        <Link
                            to={{
                                pathname: `/realestate-detail/${e.id}`,
                                estateData: e,
                            }}
                        >
                            <div className="estate-list-img-container">
                                <img
                                    src={"https://pwa.tidewater.ir:2026/estate/image/" + i}
                                    alt="realestate"
                                    className="estate-list-img"
                                />
                            </div>
                        </Link>
                    </Carousel.Item>
                )
            })
        } else {
            carouselIndicators = false
            imagesCarouselItems = (
                <Carousel.Item>
                    <img src={noImage} alt="realestate" className="estate-list-img-container" />
                </Carousel.Item>
            )
        }

        return (
            <div className="col-11 col-sm-4 mx-auto">
                <Carousel
                    interval={null}
                    prevLabel=""
                    nextLabel=""
                    nextIcon={null}
                    prevIcon={null}
                    className="mb-0"
                    defaultActiveIndex={0}
                    indicators={carouselIndicators}
                >
                    {imagesCarouselItems}
                </Carousel>
                <div className="row m-0 mt-2">
                    <div className="col-8">
                        <Link
                            to={{
                                pathname: `/realestate-detail/${e.id}`,
                                estateData: e,
                            }}
                        >
                            <h5 className="text-dark">{e.name}</h5>
                        </Link>
                    </div>
                    <div className="col-2">
                        <Button
                            variant="success"
                            className="btn btn-success fa fa-check-square"
                            onClick={() => {
                                setActiveEstate(e)
                                setAcceptance(true)
                                setAction("accept")
                                setShowConfirmationModal(true)
                            }}
                        ></Button>
                    </div>
                    <div className="col-2">
                        <Button
                            variant="danger"
                            className="btn btn-danger fa fa-trash-alt"
                            onClick={() => {
                                setActiveEstate(e)
                                setAcceptance(false)
                                setAction("reject")
                                setShowConfirmationModal(true)
                            }}
                        ></Button>
                    </div>
                </div>
                <div className="row m-0 mt-1">
                    <div className="col-12 mb-4">
                        <p className="text-secondary mb-0">{e.address}</p>
                    </div>
                </div>
            </div>
        )
    })

    return (
        <div>
            <Header backLink={"/home"} homeItemId={1} />
            <div className="home-wrapper overflow-auto py-4">
                <div className="col-10 mx-auto p-0 pt-4 pb-4">
                    <div className="row m-0 mt-5 text-secondary font-weight-bold justify-content-sm-center">
                        املاک تأیید نشده
                    </div>
                </div>
                {loadPending ? (
                    <div className="text-center">
                        <button className="btn btn-primary">
                            <span className="spinner-border"></span>
                        </button>
                    </div>
                ) : null}
                <div className="row m-0">{estatesList}</div>
                {showConfirmationModal ? (
                    <ConfirmationModal
                        handler={estateConfirmHandler}
                        show={showConfirmationModal}
                        action={action}
                        pending={pending}
                        onHide={() => {
                            setShowConfirmationModal(false)
                        }}
                    />
                ) : null}
                {showResultModal ? <ResultModal show={showResultModal} action={action} /> : null}
            </div>
        </div>
    )
}

export default RealestateUnconfirmed
