import React, { useState } from "react"
import { BsFillCameraFill } from "react-icons/bs"
import { Carousel } from "react-bootstrap"
import Header from "../components/header"
import { HomePageItems } from "../utilities/data"
import { estateCreateAsync } from "../utilities/utilities"
import { imageUploadAsync } from "../utilities/utilities"
import ConfirmationModal from "../components/confirmation-modal"
import ResultModal from "../components/result-modal"
import ErrorModal from "../components/error-modal"

const RealestateCreate = (props) => {
    const [pending, setPending] = useState(false)
    const [error, setError] = useState("")
    const [success, setSuccess] = useState(false)
    const [showConfirmationModal, setShowConfirmationModal] = useState(false)
    const [showResultModal, setShowResultModal] = useState(false)
    const [showErrorModal, setShowErrorModal] = useState(false)
    const [source, setSource] = useState({
        captureImage1: "",
        captureImage2: "",
        captureImage3: "",
    })
    const [imageFile, setImageFile] = useState({
        captureImage1: null,
        captureImage2: null,
        captureImage3: null,
    })
    const [name, setName] = useState("")
    const [address, setAddress] = useState("")
    const [group, setGroup] = useState("")
    const [areaAyan, setAreaAyan] = useState("")
    const [yearofConstruction, setYearofConstruction] = useState("")
    const [identityNumber, setIdentityNumber] = useState("")
    const [location, setLocation] = useState("")
    const [documentStatus, setDocumentStatus] = useState("")
    const [documentType, setDocumentType] = useState("")
    const [ownership, setOwnership] = useState("")
    const [beneficiary, setBeneficiary] = useState("")
    const [usage, setUsage] = useState("")
    const [areaArse, setAreaArse] = useState("")
    const [price, setPrice] = useState("")
    const [lift, setLift] = useState(false)
    const [storage, setStorage] = useState(false)
    const [parking, setParking] = useState(false)
    const company = props.location.companyName
    const title = "عنوان"
    const facilities = "امکانات"
    const role = JSON.parse(localStorage.getItem("userData")).user.role.name
    const maxYearValue = 1400

    const homeItemId = HomePageItems.find((item) => {
        return item.name === company
    })?.id

    const handleCapture = (target) => {
        if (target.files) {
            if (target.files.length !== 0) {
                const file = target.files[0]
                const newUrl = URL.createObjectURL(file)
                setImageFile({ ...imageFile, [target.name]: file })
                setSource({ ...source, [target.name]: newUrl })
            }
        }
    }
    const submitHandler = async (event) => {
        event.preventDefault()
        setPending(true)
        setShowErrorModal(false)
        try {
            if (
                !name ||
                !address ||
                !group ||
                !areaAyan ||
                !yearofConstruction ||
                !identityNumber ||
                !location ||
                !documentStatus ||
                !documentType ||
                !ownership ||
                !beneficiary ||
                !usage
            ) {
                setSuccess(false)
                setPending(false)
                setShowConfirmationModal(false)
                setError("تکمیل موارد * دار الزامی است")
                return setShowErrorModal(true)
            }
            const createdEstate = await estateCreateAsync(
                name,
                company,
                title,
                location,
                beneficiary,
                group,
                documentType,
                identityNumber.toString(),
                price.toString(),
                areaArse.toString(),
                areaAyan.toString(),
                ownership,
                facilities,
                usage,
                address,
                role,
                lift,
                parking,
                storage,
                yearofConstruction.toString()
            )
            if ("id" in createdEstate)
                await imageUploadAsync(
                    createdEstate.id,
                    imageFile.captureImage1,
                    imageFile.captureImage2,
                    imageFile.captureImage3
                )
            else {
                setSuccess(false)
                setError(createdEstate?.message)
                setPending(false)
                setShowConfirmationModal(false)
                setShowErrorModal(true)
                return
            }
            setError(false)
            setSuccess(true)
            setPending(false)
            setShowConfirmationModal(false)
            setShowResultModal(true)
            return
        } catch (error) {
            setSuccess(false)
            setError(true)
            setPending(false)
            setShowConfirmationModal(false)
            return
        }
    }

    return (
        <div>
            <Header
                backLink={`/realestate-crud/${homeItemId}`}
                homeItemId={homeItemId}
            />
            <div className="home-wrapper overflow-auto pb-4 pt-68">
                <Carousel
                    interval={null}
                    prevLabel=""
                    nextLabel=""
                    nextIcon={null}
                    prevIcon={null}
                    className="mb-0"
                    defaultActiveIndex={0}
                >
                    <Carousel.Item>
                        <label
                            htmlFor="capture-image-1"
                            className={`add-photo-container d-block ${
                                source.captureImage1 ? "h-350" : "pt-150"
                            } mx-auto`}
                        >
                            {source.captureImage1 && (
                                <div className="view-photo-container">
                                    <img
                                        src={source.captureImage1}
                                        style={{
                                            width: "100%",
                                            height: "auto",
                                        }}
                                        alt={"snap"}
                                    />
                                </div>
                            )}
                            {!source.captureImage1 && (
                                <div className="text-center">
                                    <BsFillCameraFill className="h3" />
                                    <br />
                                    عکس شماره یک
                                </div>
                            )}
                            {source.captureImage1 && (
                                <div className="image-badge">
                                    عکس شماره یک انتخاب شد
                                </div>
                            )}
                            <input
                                accept="image/*"
                                id="capture-image-1"
                                name="captureImage1"
                                type="file"
                                className="invisible h-0"
                                onChange={(e) => handleCapture(e.target)}
                                disabled={pending}
                            />
                        </label>
                    </Carousel.Item>
                    <Carousel.Item>
                        <label
                            htmlFor="capture-image-2"
                            className={`add-photo-container d-block ${
                                source.captureImage2 ? "h-350" : "pt-150"
                            } mx-auto`}
                        >
                            {source.captureImage2 && (
                                <div className="view-photo-container">
                                    <img
                                        src={source.captureImage2}
                                        style={{
                                            width: "100%",
                                            height: "auto",
                                        }}
                                        alt={"snap"}
                                    />
                                </div>
                            )}
                            {!source.captureImage2 && (
                                <div className="text-center">
                                    <BsFillCameraFill className="h3" />
                                    <br />
                                    عکس شماره دو
                                </div>
                            )}
                            {source.captureImage2 && (
                                <div className="image-badge">
                                    عکس شماره دو انتخاب شد
                                </div>
                            )}
                            <input
                                accept="image/*"
                                id="capture-image-2"
                                name="captureImage2"
                                type="file"
                                className="invisible h-0"
                                onChange={(e) => handleCapture(e.target)}
                                disabled={pending}
                            />
                        </label>
                    </Carousel.Item>
                    <Carousel.Item>
                        <label
                            htmlFor="capture-image-3"
                            className={`add-photo-container d-block ${
                                source.captureImage3 ? "h-350" : "pt-150"
                            } mx-auto`}
                        >
                            {source.captureImage3 && (
                                <div className="view-photo-container">
                                    <img
                                        src={source.captureImage3}
                                        style={{
                                            width: "100%",
                                            height: "auto",
                                        }}
                                        alt={"snap"}
                                    />
                                </div>
                            )}
                            {!source.captureImage3 && (
                                <div className="text-center">
                                    <BsFillCameraFill className="h3" />
                                    <br />
                                    عکس شماره سه
                                </div>
                            )}
                            {source.captureImage3 && (
                                <div className="image-badge">
                                    عکس شماره سه انتخاب شد
                                </div>
                            )}
                            <input
                                accept="image/*"
                                id="capture-image-3"
                                name="captureImage3"
                                type="file"
                                className="invisible h-0"
                                onChange={(e) => handleCapture(e.target)}
                                disabled={pending}
                            />
                        </label>
                    </Carousel.Item>
                </Carousel>
                <div className="col-10 mx-auto p-0 pt-2">
                    <div className="row m-0">
                        <div className="col-12 p-0 text-center text-light mb-3">
                            <ErrorModal
                                error={error}
                                showModal={showErrorModal}
                                setShowModal={setShowErrorModal}
                            />
                            <span className="bg-success">
                                {success
                                    ? "اطلاعات ملک با موفقیت ویرایش شد"
                                    : ""}
                            </span>
                        </div>
                    </div>
                    <div className="row m-0">
                        <form className="col-12 p-0">
                            <input
                                type="text"
                                placeholder="لطفا نام ملک را وارد کنید*"
                                className="form-control fs-large custom-input"
                                onChange={(e) => setName(e.target.value)}
                                value={name}
                                disabled={pending}
                            />
                            <input
                                type="text"
                                placeholder="لطفا آدرس را وارد کنید*"
                                className="form-control mt-2 custom-input"
                                onChange={(e) => setAddress(e.target.value)}
                                value={address}
                                disabled={pending}
                            />
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mb-2">
                                <label className="col-12 text-danger font-weight-bold my-3">
                                    * تکمیل موراد ستاره دار الزامی است
                                </label>
                            </div>
                            <div className="row flex-nowrap of-scroll sb-hidden text-center">
                                <div className="col-4 pl-2 pr-2 ml-3 carousel-input-wrapper">
                                    <h6 className="text-center">*گروه</h6>
                                    <select
                                        className="form-control carousel-input custom-input-border"
                                        onChange={(e) =>
                                            setGroup(e.target.value)
                                        }
                                        value={group}
                                        disabled={pending}
                                    >
                                        <option value="" selected>
                                            انتخاب کنید
                                        </option>
                                        <option value="apartmant">
                                            آپارتمان
                                        </option>
                                        <option value="mostaghelat">
                                            مستغلات
                                        </option>
                                        <option value="zamin">زمین</option>
                                        <option value="amozeshgah">
                                            آموزشگاه
                                        </option>
                                        <option value="tejari">تجاری</option>
                                    </select>
                                </div>
                                <div className="col-4 pl-2 pr-2 ml-3 carousel-input-wrapper">
                                    <h6 className="text-center">*متراژ عیان</h6>
                                    <input
                                        type="number"
                                        className="form-control carousel-input custom-input-border"
                                        onChange={(e) =>
                                            setAreaAyan(
                                                e.target.value > 0
                                                    ? e.target.value
                                                    : ""
                                            )
                                        }
                                        value={areaAyan}
                                        disabled={pending}
                                        min={0}
                                    />
                                </div>
                                <div className="col-4 pl-2 pr-2 ml-3 carousel-input-wrapper">
                                    <h6 className="text-center">*سال ساخت</h6>
                                    <input
                                        type="number"
                                        className="form-control carousel-input custom-input-border"
                                        onChange={(e) =>
                                            setYearofConstruction(
                                                e.target.value > maxYearValue
                                                    ? maxYearValue
                                                    : e.target.value
                                            )
                                        }
                                        value={yearofConstruction}
                                        disabled={pending}
                                        max={1400}
                                        min={0}
                                    />
                                </div>
                                <div className="col-4 pl-2 pr-2 ml-3 carousel-input-wrapper">
                                    <h6 className="text-center">*کد اموال</h6>
                                    <input
                                        type="number"
                                        className="form-control carousel-input custom-input-border"
                                        onChange={(e) =>
                                            setIdentityNumber(e.target.value)
                                        }
                                        value={identityNumber}
                                        disabled={pending}
                                        min={0}
                                    />
                                </div>
                                <div className="col-4 pl-2 pr-2 ml-3 carousel-input-wrapper">
                                    <h6 className="text-center">
                                        *محل استقرار
                                    </h6>
                                    <input
                                        type="text"
                                        className="form-control carousel-input custom-input-border"
                                        onChange={(e) =>
                                            setLocation(e.target.value)
                                        }
                                        value={location}
                                        disabled={pending}
                                    />
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mb-2">
                                <div className="col-6 font-weight-bold my-auto">
                                    *وضعیت سند
                                </div>
                                <div className="col-6 font-weight-bold p-0">
                                    <select
                                        className="form-control custom-input custom-input-border"
                                        onChange={(e) =>
                                            setDocumentStatus(e.target.value)
                                        }
                                        value={documentStatus}
                                        disabled={pending}
                                    >
                                        <option value="" disabled>
                                            انتخاب کنید
                                        </option>
                                        <option value="azad">آزاد</option>
                                        <option value="darrahn">در رهن</option>
                                        <option value="bazdasht">
                                            بازداشت
                                        </option>
                                    </select>
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mb-2">
                                <div className="col-6 font-weight-bold my-auto">
                                    *نوع سند
                                </div>
                                <div className="col-6 font-weight-bold p-0">
                                    <select
                                        className="form-control custom-input custom-input-border"
                                        onChange={(e) =>
                                            setDocumentType(e.target.value)
                                        }
                                        value={documentType}
                                        disabled={pending}
                                    >
                                        <option value="" selected>
                                            انتخاب کنید
                                        </option>
                                        <option value="takbarg">تک برگ</option>
                                        <option value="daftarche">
                                            دفترچه ای
                                        </option>
                                        <option value="gholname">
                                            قول نامه ای
                                        </option>
                                        <option value="sayer">سایر</option>
                                    </select>
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mb-2">
                                <div className="col-6 font-weight-bold my-auto">
                                    *مالکیت
                                </div>
                                <div className="col-6 font-weight-bold p-0">
                                    <select
                                        className="form-control custom-input custom-input-border"
                                        onChange={(e) =>
                                            setOwnership(e.target.value)
                                        }
                                        value={ownership}
                                        disabled={pending}
                                    >
                                        <option value="" selected>
                                            انتخاب کنید
                                        </option>
                                        <option value="تاید‌واتر خاورمیانه">
                                            تاید‌واتر خاورمیانه
                                        </option>
                                        <option value="بتا">بتا</option>
                                        <option value="امید پارس">
                                            امید پارس
                                        </option>
                                        <option value="امید ساحل">
                                            امید ساحل
                                        </option>
                                        <option value="سهند">سهند</option>
                                        <option value="بیکران قشم">
                                            بیکران قشم
                                        </option>
                                        <option value="رهیاب رایانه گستر">
                                            رهیاب رایانه گستر
                                        </option>
                                        <option value="تندر ایرانیان">
                                            تندر ایرانیان
                                        </option>
                                        <option value="خط دریا بندر">
                                            خط دریا بندر
                                        </option>
                                        <option value="ساحل گستر پارس">
                                            ساحل گستر پارس
                                        </option>
                                        <option value="خلیج فارس">
                                            خلیج فارس
                                        </option>
                                        <option value="ساحل و فراساحل">
                                            ساحل و فراساحل
                                        </option>
                                        <option value="سایر (در اجاره)">
                                            سایر(در اجاره)
                                        </option>
                                    </select>
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mb-2">
                                <div className="col-6 font-weight-bold my-auto">
                                    *بهره‌برداری
                                </div>
                                <div className="col-6 font-weight-bold p-0">
                                    <select
                                        className="form-control custom-input custom-input-border"
                                        onChange={(e) =>
                                            setBeneficiary(e.target.value)
                                        }
                                        value={beneficiary}
                                        disabled={pending}
                                    >
                                        <option value="" selected>
                                            انتخاب کنید
                                        </option>
                                        <option value="تاید‌واتر خاورمیانه">
                                            تاید‌واتر خاورمیانه
                                        </option>
                                        <option value="بتا">بتا</option>
                                        <option value="امید پارس">
                                            امید پارس
                                        </option>
                                        <option value="امید ساحل">
                                            امید ساحل
                                        </option>
                                        <option value="سهند">سهند</option>
                                        <option value="بیکران قشم">
                                            بیکران قشم
                                        </option>
                                        <option value="رهیاب رایانه گستر">
                                            رهیاب رایانه گستر
                                        </option>
                                        <option value="تندر ایرانیان">
                                            تندر ایرانیان
                                        </option>
                                        <option value="خط دریا بندر">
                                            خط دریا بندر
                                        </option>
                                        <option value="ساحل گستر پارس">
                                            ساحل گستر پارس
                                        </option>
                                        <option value="خلیج فارس">
                                            خلیج فارس
                                        </option>
                                        <option value="ساحل و فراساحل">
                                            ساحل و فراساحل
                                        </option>
                                        <option value="سایر (در اجاره)">
                                            سایر(در اجاره)
                                        </option>
                                    </select>
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mb-2">
                                <div className="col-6 font-weight-bold my-auto">
                                    *کاربری
                                </div>
                                <div className="col-6 font-weight-bold p-0">
                                    <select
                                        className="form-control custom-input custom-input-border"
                                        onChange={(e) =>
                                            setUsage(e.target.value)
                                        }
                                        value={usage}
                                        disabled={pending}
                                    >
                                        <option value="" selected>
                                            انتخاب کنید
                                        </option>
                                        <option value="edari">اداری</option>
                                        <option value="maskooni">مسکونی</option>
                                        <option value="tejari">تجاری</option>
                                        <option value="zamin">زمین</option>
                                        <option value="amoozeshi">
                                            آموزشی
                                        </option>
                                    </select>
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mb-2">
                                <div className="col-6 font-weight-bold my-auto">
                                    متراژ عرضه
                                </div>
                                <div className="col-6 font-weight-bold p-0">
                                    <input
                                        type="number"
                                        className="form-control custom-input custom-input-border"
                                        onChange={(e) =>
                                            setAreaArse(
                                                e.target.value > 0
                                                    ? e.target.value
                                                    : ""
                                            )
                                        }
                                        value={areaArse}
                                        disabled={pending}
                                        min={0}
                                    />
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mb-2">
                                <div className="col-6 font-weight-bold my-auto">
                                    برآورد ‌ریالی (میلیارد ریال)
                                </div>
                                <div className="col-6 font-weight-bold p-0">
                                    <input
                                        type="text"
                                        className="form-control custom-input custom-input-border"
                                        onChange={(e) =>
                                            setPrice(e.target.value)
                                        }
                                        value={price}
                                        disabled={pending}
                                    />
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="row m-0 mb-2">
                                <div className="col-12 font-weight-bold my-auto">
                                    امکانات
                                    <br />
                                    <label>
                                        <input
                                            type="checkbox"
                                            onChange={(e) =>
                                                setLift(e.target.checked)
                                            }
                                            checked={lift}
                                            disabled={pending}
                                        />
                                        <span className="text-secondary my-auto mr-2">
                                            آسانسور
                                        </span>
                                    </label>
                                    <label className="mr-2">
                                        <input
                                            type="checkbox"
                                            onChange={(e) =>
                                                setParking(e.target.checked)
                                            }
                                            checked={parking}
                                            disabled={pending}
                                        />
                                        <span className="text-secondary my-auto mr-2">
                                            پارکینگ
                                        </span>
                                    </label>
                                    <label className="mr-2">
                                        <input
                                            type="checkbox"
                                            onChange={(e) =>
                                                setStorage(e.target.checked)
                                            }
                                            checked={storage}
                                            disabled={pending}
                                        />
                                        <span className="text-secondary my-auto mr-2">
                                            انباری
                                        </span>
                                    </label>
                                </div>
                            </div>
                            <hr className="mt-2 mb-2" />
                            <div className="text-center">
                                <button
                                    type="button"
                                    className="btn btn-primary font-weight-bold col-12 mt-3"
                                    onClick={() => {
                                        setShowConfirmationModal(true)
                                    }}
                                >
                                    اضافه کردن
                                </button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
            {showConfirmationModal ? (
                <ConfirmationModal
                    handler={submitHandler}
                    show={showConfirmationModal}
                    action="createEstate"
                    pending={pending}
                    onHide={() => {
                        setShowConfirmationModal(false)
                    }}
                />
            ) : null}
            {showResultModal ? (
                <ResultModal show={showResultModal} action={"createEstate"} />
            ) : null}
        </div>
    )
}

export default RealestateCreate
