import React from "react"
import { IoIosArrowBack } from "react-icons/io"
import { Link } from "react-router-dom"
import { Nav, Navbar } from "react-bootstrap"
import insurance from "../assets/icons/insurance.png"
import realestate from "../assets/icons/realestate.png"
import vehicle from "../assets/icons/vehicle.png"
import user from "../assets/icons/user.png"
import tools from "../assets/icons/tools.png"
import { useHistory } from "react-router-dom"

const Header = ({ backLink, homeItemId, isHomePage }) => {
    const history = useHistory()
    const signUserOut = () => {
        localStorage.removeItem("userData")
        history.push("/login")
    }
    return (
        <div className="col-12 row m-0 mx-auto pt-2 bg-white position-fixed z-999">
            <div className="col-2">
                <Navbar
                    variant="light"
                    className="d-sm-block d-none position-fixed top-10-px"
                    expand="true"
                    sticky="top"
                >
                    <Navbar.Toggle
                        aria-controls="basic-navbar-nav"
                        className="ml-auto bg-white"
                    />
                    <Navbar.Collapse
                        id="basic-navbar-nav"
                        className="py-2 px-1 bg-white rounded text-right shadow-sm w-200-px"
                    >
                        <Nav className="ml-auto">
                            <Link
                                to="/home"
                                className="h6 my-auto text-dark font-weight-bold p-2 border-bottom add-light-gray-bg-on-hover"
                            >
                                <img
                                    src={realestate}
                                    className="bottom-nav-bar-icon"
                                    alt="Icon"
                                />
                                <span className="align-middle mr-3">املاک</span>
                            </Link>
                            <Link
                                to="/home"
                                className="h6 my-auto text-dark font-weight-bold p-2 border-bottom add-light-gray-bg-on-hover"
                            >
                                <img
                                    src={vehicle}
                                    className="bottom-nav-bar-icon"
                                    alt="Icon"
                                />
                                <span className="align-middle mr-3">خودرو</span>
                            </Link>
                            <Link
                                to="/profile"
                                className="h6 my-auto text-dark font-weight-bold p-2 border-bottom add-light-gray-bg-on-hover"
                            >
                                <img
                                    src={user}
                                    className="bottom-nav-bar-icon"
                                    alt="Icon"
                                />
                                <span className="align-middle mr-3">
                                    پروفایل
                                </span>
                            </Link>
                            <Link
                                to="/home"
                                className="h6 my-auto text-dark font-weight-bold p-2 border-bottom add-light-gray-bg-on-hover"
                            >
                                <img
                                    src={tools}
                                    className="bottom-nav-bar-icon"
                                    alt="Icon"
                                />
                                <span className="align-middle mr-3">
                                    تجهیزات
                                </span>
                            </Link>
                            <Link
                                to="/home"
                                className="h6 my-auto text-dark font-weight-bold p-2 add-light-gray-bg-on-hover"
                            >
                                <img
                                    src={insurance}
                                    className="bottom-nav-bar-icon"
                                    alt="Icon"
                                />
                                <span className="align-middle mr-3">بیمه</span>
                            </Link>
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>
            </div>
            <Link
                to="/home"
                className={`img-fluid col-8 home-item-${homeItemId} realestate-item`}
            ></Link>

            {isHomePage ? (
                <div
                    className="col-2 my-auto p-0 text-left"
                    onClick={signUserOut}
                >
                    <svg
                        class="text-danger btn-exit"
                        fill="none"
                        stroke="currentColor"
                        viewBox="0 0 24 24"
                        xmlns="http://www.w3.org/2000/svg"
                    >
                        <path
                            stroke-linecap="round"
                            stroke-linejoin="round"
                            stroke-width="2"
                            d="M17 16l4-4m0 0l-4-4m4 4H7m6 4v1a3 3 0 01-3 3H6a3 3 0 01-3-3V7a3 3 0 013-3h4a3 3 0 013 3v1"
                        ></path>
                    </svg>
                </div>
            ) : (
                <Link
                    to={`${backLink}`}
                    className="col-2 my-auto p-0 text-left"
                >
                    <IoIosArrowBack className="text-dark h3" />
                </Link>
            )}
        </div>
    )
}

export default Header
