import React, { useState } from "react"
import warning from "../assets/icons/warning.png"
import { Modal } from "react-bootstrap"
import { estateSellAsync } from "../utilities/utilities"
import ResultModal from "./result-modal"
import { MAX_YEAR } from "../utilities/data"

function SellModal(props) {
    const activeEstate = props.activeEstate
    const [sellDay, setSellDay] = useState("")
    const [sellMonth, setSellMonth] = useState("")
    const [sellYear, setSellYear] = useState("")
    const [buyer, setBuyer] = useState("")
    const [pending, setPending] = useState(false)
    const [showSellModal, setShowSellModal] = useState(true)
    const [showResultModal, setShowResultModal] = useState(false)

    const estateSellHandler = async (event) => {
        event.preventDefault()
        setPending(true)
        const id = activeEstate.id
        const date = sellYear + "-" + sellMonth + "-" + sellDay
        try {
            await estateSellAsync(id, buyer, date)
            setPending(false)
            setShowSellModal(false)
            setShowResultModal(true)
        } catch (error) {
            setPending(false)
            console.log(error)
        }
    }
    return (
        <div>
            <Modal
                {...props}
                show={showSellModal}
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Modal.Header closeButton></Modal.Header>
                <Modal.Body>
                    <div className="row m-0">
                        <div className="col-12 text-center">
                            <img
                                src={warning}
                                alt="warning"
                                className="warning-icon mb-3"
                            />
                            <h4 className="text-center mb-3">
                                عملیات فروش ملک
                            </h4>
                            <h5 className="text-center mb-3">
                                آیا مطمئن هستید؟
                            </h5>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-11 mx-auto">
                            <input
                                type="number"
                                className="modal-input"
                                placeholder="روز"
                                onChange={(e) =>
                                    setSellDay(
                                        e.target.value > 0
                                            ? e.target.value < 32
                                                ? e.target.value
                                                : 30
                                            : ""
                                    )
                                }
                                value={sellDay}
                                min={1}
                                max={31}
                            />
                            <input
                                type="number"
                                className="modal-input"
                                placeholder="ماه"
                                onChange={(e) =>
                                    setSellMonth(
                                        e.target.value > 0
                                            ? e.target.value < 13
                                                ? e.target.value
                                                : 2
                                            : ""
                                    )
                                }
                                value={sellMonth}
                                min={1}
                                max={12}
                            />
                            <input
                                type="number"
                                className="modal-input"
                                placeholder="سال"
                                onChange={(e) =>
                                    setSellYear(
                                        e.target.value > 0
                                            ? e.target.value <= MAX_YEAR
                                                ? e.target.value
                                                : MAX_YEAR
                                            : ""
                                    )
                                }
                                value={sellYear}
                                min={1}
                                max={MAX_YEAR}
                            />
                            <input
                                type="text"
                                className="modal-input"
                                placeholder="خریدار"
                                onChange={(e) => setBuyer(e.target.value)}
                                value={buyer}
                            />
                            <input
                                type="number"
                                className="modal-input"
                                placeholder="شماره مزایده"
                            />
                        </div>
                    </div>
                </Modal.Body>
                <Modal.Footer>
                    <button
                        type="button"
                        className="btn btn-secondary btn-lg"
                        onClick={props.onHide}
                        disabled={pending}
                    >
                        بازگشت
                    </button>
                    <button
                        type="button"
                        className="btn btn-danger btn-lg"
                        onClick={estateSellHandler}
                    >
                        {pending ? (
                            <span className="spinner-border spinner-border-sm ml-2"></span>
                        ) : null}
                        فروش
                    </button>
                </Modal.Footer>
            </Modal>
            <ResultModal show={showResultModal} action={"sell"} />
        </div>
    )
}

export default SellModal
