import React, { useEffect, useState } from "react"
import { useParams } from "react-router"
import Header from "../components/header"
import SellModal from "../components/sell-modal"
import { estateAsync, getUserRole } from "../utilities/utilities"
import { Carousel, Button } from "react-bootstrap"
import { Link } from "react-router-dom"
import { HomePageItems } from "../utilities/data"
import noImage from "../assets/no-image.png"
import { FaShoppingBasket } from "react-icons/fa"

const RealestateList = () => {
    const { companyName } = useParams()
    let homeItemId
    if (companyName) {
        homeItemId = HomePageItems.find((item) => {
            return item.name === companyName
        }).id
    }
    const [estates, setEstates] = useState([])
    const [pending, setPending] = useState(false)
    const [modalShow, setModalShow] = useState(false)
    const [activeEstate, setActiveEstate] = useState({})
    useEffect(() => {
        ;(async () => {
            if (companyName) {
                setPending(true)
                const fetchedEstates = await estateAsync(companyName)
                setEstates(fetchedEstates)
                setPending(false)
            } else {
                setPending(true)
                const fetchedEstates = await estateAsync()
                setEstates(fetchedEstates)
                setPending(false)
            }
        })()
    }, [companyName])

    const estatesList = estates.map((e) => {
        let images = []
        if (e.image1) {
            images.push(e.image1)
        }
        if (e.image2) {
            images.push(e.image2)
        }
        if (e.image3) {
            images.push(e.image3)
        }

        let imagesCarouselItems
        let carouselIndicators = true

        if (images.length !== 0) {
            imagesCarouselItems = images.map((i) => {
                return (
                    <Carousel.Item key={i}>
                        <Link
                            to={{
                                pathname: `/realestate-detail/${e.id}`,
                                estateData: e,
                            }}
                        >
                            <div className="estate-list-img-container">
                                <img
                                    src={"https://pwa.tidewater.ir:2026/estate/image/" + i}
                                    alt="realestate"
                                    className="estate-list-img"
                                />
                            </div>
                        </Link>
                    </Carousel.Item>
                )
            })
        } else {
            carouselIndicators = false
            imagesCarouselItems = (
                <Carousel.Item>
                    <img src={noImage} alt="realestate" className="estate-list-img-container" />
                </Carousel.Item>
            )
        }

        return (
            <div className="col-11 col-sm-4 mx-auto" key={e.id}>
                <Carousel
                    interval={null}
                    prevLabel=""
                    nextLabel=""
                    nextIcon={null}
                    prevIcon={null}
                    className="mb-0"
                    defaultActiveIndex={0}
                    indicators={carouselIndicators}
                >
                    {imagesCarouselItems}
                </Carousel>
                <div className="row m-0 mt-2">
                    <div className="col-8">
                        <Link
                            to={{
                                pathname: `/realestate-detail/${e.id}`,
                                estateData: e,
                            }}
                        >
                            <h5 className="text-dark">{e.name}</h5>
                        </Link>
                    </div>
                    <div className="col-2">
                        {getUserRole() === 2 || getUserRole() === 4 ? (
                            <div>
                                <span className="btn btn-secondary fa fa-edit"></span>
                            </div>
                        ) : (
                            <Link
                                to={{
                                    pathname: "/realestate-edit",
                                    estateData: e,
                                }}
                            >
                                <span className="btn btn-primary fa fa-edit"></span>
                            </Link>
                        )}
                    </div>
                    <div className="col-2">
                        {getUserRole() === 2 || getUserRole() === 4 || getUserRole() === 5 ? (
                            <Button className="btn btn-secondary">
                                <FaShoppingBasket />
                            </Button>
                        ) : (
                            <Button
                                variant="danger"
                                className="btn btn-danger"
                                onClick={() => {
                                    setActiveEstate(e)
                                    setModalShow(true)
                                }}
                            >
                                <FaShoppingBasket />
                            </Button>
                        )}
                    </div>
                </div>
                <div className="row m-0 mt-1">
                    <div className="col-12">
                        <p className="text-secondary">{e.address}</p>
                    </div>
                </div>
            </div>
        )
    })

    return (
        <div>
            <Header
                backLink={companyName ? "/realestate-crud/" + homeItemId : "/home"}
                homeItemId={homeItemId || 1}
            />
            <div className="home-wrapper overflow-auto py-4">
                <div className="col-10 mx-auto p-0 pt-4 pb-4">
                    <div className="row m-0 mt-5 text-secondary font-weight-bold justify-content-sm-center ">
                        املاک
                    </div>
                </div>
                {pending ? (
                    <div className="text-center">
                        <button className="btn btn-primary">
                            <span className="spinner-border"></span>
                        </button>
                    </div>
                ) : null}
                <div className="row m-0">{estatesList}</div>
                {modalShow ? (
                    <SellModal
                        activeEstate={activeEstate}
                        onHide={() => {
                            setModalShow(false)
                        }}
                    />
                ) : null}
            </div>
        </div>
    )
}

export default RealestateList
