import React, { useEffect, useState } from "react"
import { Link } from "react-router-dom"
import Header from "../components/header"
import { usersAsync } from "../utilities/utilities"

const Users = () => {
    const [users, setUsers] = useState([])
    const [pending, setPending] = useState([])

    const createUserItem = (id, name, styles, userData) => {
        return (
            <Link
                to={{
                    pathname: `users/${id}`,
                    userData: userData,
                }}
            >
                <div className={`w-100 sp-user-item ${styles}`} key={name}>
                    <br />
                    <div className="text-left">{name}</div>
                </div>
            </Link>
        )
    }

    useEffect(() => {
        ;(async () => {
            setPending(true)
            const fetchedUsers = await usersAsync()
            setUsers(fetchedUsers)
            setPending(false)
        })()
    }, [])

    const usersList = users.map((u) => {
        return (
            <div className="col-12 col-sm-4 mb-4">
                {createUserItem(u.id, u.username, "sp-user-item-x", u)}
            </div>
        )
    })

    return (
        <div className="home-wrapper">
            <Header backLink={"/profile"} homeItemId={1} />
            <br />
            <div className="pt-5">
                <div className="col-10 mx-auto p-0 pt-4 pb-5">
                    <div className="row m-0 mb-4">
                        <div className="font-weight-bold text-secondary text-right mb-2">
                            لیست کاربران
                        </div>
                    </div>
                    {pending ? (
                        <div className="text-center">
                            <button class="btn btn-primary">
                                <span class="spinner-border"></span>
                            </button>
                        </div>
                    ) : null}
                    <div className="row m-0">{usersList}</div>
                </div>
            </div>
        </div>
    )
}

export default Users
