import React, { useState } from "react"
import user from "../assets/icons/user.png"
import Header from "../components/header"
import { companyList } from "../utilities/data"
import { roleList } from "../utilities/data"
import { editUserAsync, loginAsync } from "../utilities/utilities"
import ConfirmationModal from "../components/confirmation-modal"
import ResultModal from "../components/result-modal"

const EditUser = (props) => {
    const [username, setUsername] = useState(props.location.formData.username)
    const [password, setPassword] = useState(props.location.formData.password)
    const [firstname, setFirstname] = useState(props.location.formData.firstname)
    const [lastname, setLastname] = useState(props.location.formData.lastname)
    const [personnelCode, setPersonnelCode] = useState(props.location.formData.personnelCode)
    const [phoneNumber, setPhoneNumber] = useState(props.location.formData.phoneNumber)
    const [role, setRole] = useState(props.location.formData.role)
    const [company, setCompany] = useState(props.location.formData.company)
    const [error, setError] = useState(false)
    const [pending, setPending] = useState(false)
    const [showConfirmationModal, setShowConfirmationModal] = useState(false)
    const [showResultModal, setShowResultModal] = useState(false)
    const id = JSON.parse(localStorage.getItem("userData")).user.id.toString()
    const backPath = props.location.backPath

    const roleOptions = roleList.map((r) => {
        return <option value={r.id}>{r.name}</option>
    })

    const companyOptions = companyList.map((c) => {
        return <option value={c.name}>{c.name}</option>
    })

    const submitHandler = async (e) => {
        e.preventDefault()
        setPending(true)
        let editedUser
        try {
            await editUserAsync(
                id,
                username,
                password,
                phoneNumber,
                role,
                company,
                firstname,
                lastname,
                personnelCode
            )
            editedUser = await loginAsync(username, password)
            localStorage.removeItem("userData")
            localStorage.setItem("userData", JSON.stringify(editedUser))
            setPending(false)
            setShowConfirmationModal(false)
            setShowResultModal(true)
        } catch (error) {
            setError(true)
            setPending(false)
            console.log(error)
        }
    }

    return (
        <div className="home-wrapper">
            <Header backLink={backPath} homeItemId={1} />
            <div>
                <div className="col-10 mx-auto p-0 pt-4 pb-5">
                    <div className="row m-0">
                        <div className="font-weight-bold text-secondary text-right mb-2">
                            ویرایش کاربر
                        </div>
                    </div>
                    <div className="row mt-4">
                        <img
                            className="mx-auto"
                            src={user}
                            style={{ width: "70px", height: "70px" }}
                            alt="Icon"
                        />
                    </div>
                    <div>
                        <form>
                            <div className="row m-0">
                                <div className="col-12 text-center mt-4 bg-danger text-light">
                                    {error ? "خطا در ویرایش اطلاعات کاربر" : ""}
                                </div>
                            </div>
                            <div className="row m-0 mt-5">
                                <div className="col-6 p-0 mb-4 col-sm-3 font-weight-bold">
                                    نام کاربری
                                </div>
                                <div className="col-6 p-0 mb-4 col-sm-3 text-secondary">
                                    <input
                                        name="username"
                                        type="text"
                                        className="sp-add-user-input"
                                        required
                                        disabled={pending}
                                        onChange={(event) => {
                                            setUsername(event.target.value)
                                        }}
                                        value={username}
                                    />
                                </div>
                                <div className="col-6 p-0 mb-4 col-sm-3 font-weight-bold">
                                    رمز عبور
                                </div>
                                <div className="col-6 p-0 mb-4 col-sm-3 text-secondary">
                                    <input
                                        name="password"
                                        type="password"
                                        className="sp-add-user-input"
                                        required
                                        disabled={pending}
                                        onChange={(event) => {
                                            setPassword(event.target.value)
                                        }}
                                        value={password}
                                    />
                                </div>
                                <div className="col-6 p-0 mb-4 col-sm-3 font-weight-bold">نام</div>
                                <div className="col-6 p-0 mb-4 col-sm-3 text-secondary">
                                    <input
                                        name="firstname"
                                        type="text"
                                        className="sp-add-user-input"
                                        required
                                        disabled={pending}
                                        onChange={(event) => {
                                            setFirstname(event.target.value)
                                        }}
                                        value={firstname}
                                    />
                                </div>

                                <div className="col-6 p-0 mb-4 col-sm-3 font-weight-bold">
                                    نام خانوادگی
                                </div>
                                <div className="col-6 p-0 mb-4 col-sm-3 text-secondary">
                                    <input
                                        name="lastname"
                                        type="text"
                                        className="sp-add-user-input"
                                        required
                                        disabled={pending}
                                        onChange={(event) => {
                                            setLastname(event.target.value)
                                        }}
                                        value={lastname}
                                    />
                                </div>

                                <div className="col-6 p-0 mb-4 col-sm-3 font-weight-bold">
                                    کد پرسنلی
                                </div>
                                <div className="col-6 p-0 mb-4 col-sm-3 text-secondary">
                                    <input
                                        name="personnelCode"
                                        type="text"
                                        className="sp-add-user-input"
                                        required
                                        disabled={pending}
                                        onChange={(event) => {
                                            setPersonnelCode(event.target.value)
                                        }}
                                        value={personnelCode}
                                    />
                                </div>

                                <div className="col-6 p-0 mb-4 col-sm-3 font-weight-bold">
                                    شماره موبایل
                                </div>
                                <div className="col-6 p-0 mb-4 col-sm-3 text-secondary">
                                    <input
                                        name="phoneNumber"
                                        type="text"
                                        className="sp-add-user-input"
                                        required
                                        disabled={pending}
                                        onChange={(event) => {
                                            setPhoneNumber(event.target.value)
                                        }}
                                        value={phoneNumber}
                                    />
                                </div>

                                <div className="col-6 p-0 mb-4 col-sm-3 font-weight-bold">سمت</div>
                                <div className="col-6 p-0 mb-4 col-sm-3 text-secondary">
                                    <select
                                        name="role"
                                        id="role"
                                        required
                                        disabled
                                        onChange={(event) => {
                                            setRole(event.target.value)
                                        }}
                                        value={role}
                                        className="form-control"
                                    >
                                        {roleOptions}
                                    </select>
                                </div>

                                <div className="col-6 p-0 mb-4 col-sm-3 font-weight-bold">شرکت</div>
                                <div className="col-6 p-0 mb-4 col-sm-3 text-secondary">
                                    <select
                                        name="company"
                                        id="company"
                                        onChange={(event) => {
                                            setCompany(event.target.value)
                                        }}
                                        value={company}
                                        required
                                        disabled={pending}
                                        className="form-control"
                                    >
                                        {companyOptions}
                                    </select>
                                </div>
                            </div>
                            <div className="row m-0 mt-4">
                                <button
                                    type="button"
                                    className="btn btn-primary mt-4 mx-auto col-12"
                                    onClick={() => {
                                        setShowConfirmationModal(true)
                                    }}
                                >
                                    ذخیره
                                </button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
            {showConfirmationModal ? (
                <ConfirmationModal
                    handler={submitHandler}
                    show={showConfirmationModal}
                    action={"editUser"}
                    pending={pending}
                    onHide={() => {
                        setShowConfirmationModal(false)
                    }}
                />
            ) : null}
            {showResultModal ? <ResultModal show={showResultModal} action={"editUser"} /> : null}
        </div>
    )
}

export default EditUser
